* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Inter', sans-serif;
}

.container {
    padding: 50px 100px 0 100px;
}

.Hero--banner .container {
    display: grid;
    grid-gap: 30px;
    grid-template-columns: auto auto;
}


nav.nav-menu {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    background-color: #F55A5A;
}

.nav-menu .logo {
    font-size: clamp(1.5rem, 2.5vw, 1.9rem);
}

.nav-menu h1 {
    font-size: clamp(1rem, 2.5vw, 1.5rem);
    font-weight: 600;
    margin: 20px 10px;
    text-transform: capitalize;
}

.card {
    min-height: 250px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    border-radius: 6px;
    box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);
}

.card img {
    max-width: 127px;
    width: 100%;
    height: 168px;
    object-fit: cover;
    border-radius: 6px;
    margin-inline-end: 15px;
}

.--location {
    display: flex;
    justify-content: safe;
    align-items: center;
    margin-block-end: .1em;
    font-size: clamp(10.24px, 2.5vw, .7rem);
}

.--location .location--icon {
    color: #F55A5A;
}


.--location a {
    color: #918E9B;
}

h1.title {
    /* line-height: 1.9; */
    font-weight: 600;
    margin-block-end: .4em;
    font-size: clamp(25px, 2.5vw, .7rem);
}

p.date {
    margin-block-end: .6em;
    font-size: clamp(10.24px, 2.5vw, .7rem);
    font-weight: 600;
}

p.description {
    font-size: clamp(10.24px, 2.5vw, .8rem);
    color: #2B283A;
}

/* @media */
@media (max-width:786px) {
    .Hero--banner .container {
        grid-template-columns: auto;
    }
}

@media (max-width:998px) {
    .container {
        padding: 50px 20px;
    }

}